<template>
    <el-drawer
    :title="this.$t('titles.waypointDetail')"
    :visible.sync="drawer"
    :close-on-press-escape="false"
    custom-class="authorBox"
    :wrapperClosable="closable"
    :show-close="showClose"
    :direction="direction"
    @close="closeDrawer">
        <div class="detailBox">
            <div class="goHere" @click.self="goHere()"></div>
            <div class="editBox">
                <el-switch v-model="edit" inactive-text="修改" active-text="" active-color="#409EFF"></el-switch>
            </div>
            <!-- 站点详情 -->
            <el-form ref="ruleForm"  class="waypointForm">
                <el-form-item class="">
                    <div class="flex formItem">
                        <div class="itemLeft">{{this.$t("titles.waypointName")}}</div>
                        <div class="itemRight"><input type="text" :placeholder="this.$t('placeholder.waypointName')" v-model="waypointForm.name" :disabled="!edit"></div>
                    </div>
                </el-form-item>

                <el-form-item>
                    <div class="flex formItem">
                        <div class="itemLeft">{{ this.$t("titles.waypointType")}}</div>
                        <div class="itemRight">
                            <el-select v-model="waypointForm.type" :placeholder="this.$t('placeholder.waypointType')" @change="changeStationType" size="mini" class="stationType" popper-class="selectClass" :disabled="!edit">
                                <el-option
                                v-for="item in waypointForm.stationType"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                </el-form-item>

                <div class="showMore" v-show="showMore" @click="showMoreBtn">查看更多<i class="el-icon-d-arrow-right moreIcon"></i></div>
                <div v-show="!showMore">
                    <!-- 默认站点模式 -->
                    <el-form-item class="" v-show="showStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">{{ this.$t("titles.waypointMode")}}</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.mode" :placeholder="this.$t('placeholder.waypointMode')" size="mini" class="stationType" popper-class="selectClass" :disabled="!edit">
                                    <el-option
                                    v-for="item in waypointForm.stationMode"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <!-- 站点列表模式 -->
                    <el-form-item class="" v-show="showGotoStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">{{ this.$t("titles.waypointMode")}}</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.gotoMode" :placeholder="this.$t('placeholder.waypointMode')" size="mini" class="stationType" :disabled="!edit">
                                    <el-option
                                    v-for="item in this.$store.state.stationList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.name">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <!-- call列表模式 -->
                    <el-form-item class="" v-show="showCallStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">{{ this.$t("titles.waypointMode")}}</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.callMode" :placeholder="this.$t('placeholder.waypointMode')" size="mini" class="stationType" :disabled="!edit">
                                    <el-option
                                    v-for="item in this.$store.state.trajectoryList"
                                    :key="item.name"
                                    :label="item.name"
                                    :value="item.name">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <!-- 声音站点模式 -->
                    <el-form-item class="" v-show="showVoiceStationMode">
                        <div class="flex formItem">
                            <div class="itemLeft">{{ this.$t("titles.waypointMode")}}</div>
                            <div class="itemRight">
                                <el-select v-model="waypointForm.voiceMode" :placeholder="this.$t('placeholder.waypointMode')" size="mini" class="stationType" :disabled="!edit">
                                    <el-option
                                    v-for="item in waypointForm.voiceStationMode"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </div>
                        </div>
                    </el-form-item>

                    <el-form-item class="" v-show="showFrameId">
                        <div class="flex formItem">
                            <div class="itemLeft">frame_id</div>
                            <div class="itemRight"><input type="text" :placeholder="this.$t('placeholder.frame_id')" v-model="waypointForm.frameId" :disabled="!edit"></div>
                        </div>
                    </el-form-item>

                    <el-form-item class="" v-show="showSubscribeValue">
                        <div class="flex formItem">
                            <div class="itemLeft">{{ this.$t("titles.pubSubVal")}}</div>
                            <div class="itemRight"><input type="text" :placeholder="this.$t('placeholder.pubSubVal')" v-model="waypointForm.subscribeValue" :disabled="!edit"></div>
                        </div>
                    </el-form-item>

                    <!-- <el-form-item class="">
                        <div class="flex formItem">
                            <div class="itemLeft">{{ this.$t("titles.scope")}}</div>
                            <div class="itemRight"><input type="text" :placeholder="this.$t('placeholder.scope')" v-model="waypointForm.scope" :disabled="!edit"></div>
                        </div>
                    </el-form-item> -->

                    <!-- <el-form-item class="">
                        <div class="flex formItem">
                            <div class="itemLeft">{{ this.$t("titles.timeout")}}</div>
                            <div class="itemRight"><input type="text" :placeholder="this.$t('placeholder.timeout')" v-model="waypointForm.timeout" :disabled="!edit"></div>
                        </div>
                    </el-form-item> -->

                    <el-form-item class="" v-show="showPosition">
                        <div class="localTitle">Positon</div>
                        <div class="flex formItem bottomNone">
                            <div class="localInputItem">
                                <div class="itemLeft">x</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="" v-model="waypointForm.positionX" :disabled="!edit">
                                </div>
                            </div>

                            <div class="localInputItem">
                                <div class="itemLeft">y</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="" v-model="waypointForm.positionY" :disabled="!edit">
                                </div>
                            </div>

                            <div class="localInputItem">
                                <div class="itemLeft">z</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="" v-model="waypointForm.positionZ" :disabled="!edit">
                                </div>
                            </div>
                        </div>
                    </el-form-item>

                    <el-form-item class="" v-show="showOrientation">
                        <div class="localTitle">Orientation</div>
                        <div class="flex formItem bottomNone">
                            <div class="localInputItem">
                                <div class="itemLeft">x</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="" v-model="waypointForm.orientationX"  @change="changeOrientation" :disabled="!edit">
                                </div>
                            </div>
                            <div class="localInputItem">
                                <div class="itemLeft">y</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="" v-model="waypointForm.orientationY"  @change="changeOrientation" :disabled="!edit">
                                </div>
                            </div>
                            <div class="localInputItem">
                                <div class="itemLeft">z</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="" v-model="waypointForm.orientationZ"  @change="changeOrientation" :disabled="!edit">
                                </div>
                            </div>
                            <div class="localInputItem">
                                <div class="itemLeft">w</div>
                                <div class="itemRight">
                                    <input type="text" placeholder="" v-model="waypointForm.orientationW"  @change="changeOrientation" :disabled="!edit">
                                </div>
                            </div>
                        </div>
                    </el-form-item>
        
                    <el-form-item class="" v-show="showYaw">
                        <div class="flex formItem">
                            <div class="itemLeft">Yaw(deg)</div>
                            <div class="itemRight"><input type="text" :placeholder="this.$t('placeholder.yaw')" v-model="waypointForm.yaw" @change="changeYaw" @keyup.enter="subUpdate" :disabled="!edit"></div>
                        </div>
                    </el-form-item>
                </div>
                <div class="flex formBtns" v-show="edit">
                    <el-button type="primary" @click="subUpdate">{{ this.$t("buttons.submit")}}</el-button>
                </div>
            </el-form>

        </div>
    </el-drawer>
</template>

<script>
import { quaternionToYaw } from '../js/methods'
import { showWaypointDetail, submitAddWaypoint } from '../js/waypoint'
import { navNewStation } from '../js/navigation'

export default {
    name: 'waypointDetail',
    components:{

    },
    data(){
        return{
            drawer: this.$store.state.showWaypointDetail,
            direction: 'btt',
            closable: true,
            showClose: false,
            
            edit: false,
            showMore: true,
            waypointForm:{
                name:'',
                stationType: [{
                    value: 'goal',
                    label: this.$t("waypointsType.goal")
                }, {
                    value: 'dock',
                    label: this.$t("waypointsType.dock")
                }, {
                    value: 'timer',
                    label: this.$t("waypointsType.timer")
                }, {
                    value: 'publisher',
                    label: this.$t("waypointsType.publisher")
                }, {
                    value: 'subscriber',
                    label: this.$t("waypointsType.subscriber")
                }, {
                    value: 'looper',
                    label: this.$t("waypointsType.looper")
                }, {
                    value: 'pubsuber',
                    label: this.$t("waypointsType.pubsuber")
                }, {
                    value: 'cmd_vel_set_sub',
                    label: this.$t("waypointsType.maximumSpeedSetting")
                }, {
                    value: 'cmd_vel',
                    label: this.$t("waypointsType.speedPublisher")
                }, {
                    value: 'shell',
                    label: this.$t("waypointsType.commandPublisher")
                }, {
                    value: 'sound_play',
                    label: this.$t("waypointsType.soundPlay")
                }, {
                    value: 'initial_pose',
                    label: this.$t("waypointsType.initialPose")
                }, {
                    value: 'scan_marker',
                    label: this.$t("waypointsType.chargingPosition")
                }, {
                    value: 'shell_string',
                    label: this.$t("waypointsType.shellCommand")
                }, {
                    value: 'pallet',
                    label: 'Pallet'
                }, {
                    value: 'bias',
                    label: 'Bias'
                }, {
                    value: 'call',
                    label: 'Call'
                }, {
                    value: 'goto',
                    label: 'Goto'
                }],
                type: "goal",
                stationMode:[{
                    value: 'LOOP',
                    label: 'LOOP',
                }, {
                    value: 'LOOP2',
                    label: 'LOOP2'
                }, {
                    value: 'BACK',
                    label: 'BACK'
                }, {
                    value: 'BACK2',
                    label: 'BACK2'
                }, {
                    value: 'NONE',
                    label: this.$t("waypointsType.once"),
                }],
                mode: 'LOOP',
                gotoMode: '',
                callMode: '',
                voiceMode: 'play',
                voiceStationMode:[{
                    value: 'LOOP',
                    label: this.$t("waypointsType.play"),
                }, {
                    value: 'LOOP2',
                    label: this.$t("waypointsType.stop"),
                }, {
                    value: 'BACK',
                    label: this.$t("waypointsType.playOnce"),
                },],
                frameId:'map',
                subscribeValue: 0,
                positionX: 0.0,
                positionY: 0.0,
                positionZ: 0.0,
                orientationX: 0.0,
                orientationY: 0.0,
                orientationZ: 0.0,
                orientationW: 0.0,
                yaw: 0.0,
                scope: 0.0,
                timeout: 0.0
            },
            showFrameId: true,
            showSubscribeValue: false,
            showStationMode: true,
            showVoiceStationMode: false,
            showGotoStationMode: false,
            showCallStationMode: false,
            showPosition: true,
            showOrientation: true,
            showYaw:true
        }
    },
    methods:{
        //点击站点去这里
        goHere(){
            this.$notify({
                title: this.$t("titles.success"),
                message: this.$t("messages.navWaypoint") + " " + this.waypointForm.name,
                type: 'success',
                position: 'top-left',
                offset: 100
            });
            navNewStation(this.waypointForm.name);
            showWaypointDetail(false);
        },

        showMoreBtn:function(){
            this.showMore = false;
        },

        //选择站点类型
        changeStationType(type){
            let that = this;
            that.showFrameId = false;
            that.showSubscribeValue = false;
            that.showStationMode = true,
            that.showVoiceStationMode = false;
            that.showGotoStationMode = false;
            that.showCallStationMode = false;
            that.showPosition = true;
            that.showOrientation = true;
            that.showYaw = true;
            switch(type){
                case 'goal':
                that.showFrameId = true;
                break;

                case 'pubsuber':
                that.showSubscribeValue = true;
                break;

                case 'sound_play':
                that.showStationMode = false;
                that.showVoiceStationMode = true;
                that.showGotoStationMode = false;
                that.showCallStationMode = false;
                break;

                case 'goto':
                that.showStationMode = false;
                that.showVoiceStationMode = false;
                that.showGotoStationMode = true;
                that.showCallStationMode = false;
                break;

                case 'call':
                that.showStationMode = false;
                that.showVoiceStationMode = false;
                that.showGotoStationMode = false;
                that.showCallStationMode = true;
                break;

                case 'initial_pose':
                that.showFrameId = true;
                break;

                default:
                
                break;
            }
        },

        //改变orientation
        changeOrientation(){
            let that = this;
            let orientation = {
                x: parseFloat(this.waypointForm.orientationX),
                y: parseFloat(this.waypointForm.orientationY),
                z: parseFloat(this.waypointForm.orientationZ),
                w: parseFloat(this.waypointForm.orientationW),
            }
            //有效的四元数转换
            if (isQuaternionValid(orientation)){
                let yawRad = quaternionToYaw(orientation);
                let yawDeg = yawRad * 180 / Math.PI;
                that.waypointForm.yaw = yawDeg.toFixed(5);
            }else{
                this.$notify({
                    title: that.$t("titles.warning"),
                    message: that.$t("messages.QuaternionsNotLegal"),
                    type: 'warning',
                    position: 'top-left',
                    offset: 100
                });
                return
            }
        },

        /**
         * 改变yaw,转换为四元数
         * **/
        changeYaw(){
            let that = this;
            let yawDeg = parseFloat(that.waypointForm.yaw);
            let orientation = thetaToQuaternion(yawDeg/180*Math.PI);
            that.waypointForm.orientationX = orientation.x.toFixed(5);
            that.waypointForm.orientationY = orientation.y.toFixed(5);
            that.waypointForm.orientationZ = orientation.z.toFixed(5);
            that.waypointForm.orientationW = orientation.w.toFixed(5);
        },

        //提交
        subUpdate() {
            if(this.waypointForm.type === "goal"){
                this.waypointForm.type = 'goal'
            }
            if(this.waypointForm.mode === 'LOOP'){
                this.waypointForm.mode = 'LOOP'
            }
            if(this.waypointForm.voiceMode === 'play'){
                this.waypointForm.voiceMode = 'LOOP'
            }
            submitAddWaypoint(this.waypointForm);
            showWaypointDetail(false);
      },

        //初始化站点参数
        updateWaypointMsg(waypointInfo){
            console.log(waypointInfo)
            //站点类型转换
            let details = JSON.parse(waypointInfo.header.frame_id);
            this.waypointForm.name = waypointInfo.name;
            this.waypointForm.type = details.type;
            this.waypointForm.mode = details.failure_mode;
            // if(details.close_enough){
            //     this.waypointForm.scope = Number(details.close_enough).toFixed(3);
            // }else{
            //     this.waypointForm.scope = Number(waypointInfo.close_enough).toFixed(3);
            // }

            // if(details.goal_timeout){
            //     this.waypointForm.timeout = Number(details.goal_timeout).toFixed(3);
            // }else{
            //     this.waypointForm.timeout = Number(waypointInfo.goal_timeout).toFixed(3)
            // }
            this.waypointForm.positionX = Number(waypointInfo.pose.position.x).toFixed(3);
            this.waypointForm.positionY = Number(waypointInfo.pose.position.y).toFixed(3);
            this.waypointForm.positionZ = Number(waypointInfo.pose.position.z).toFixed(3);
            this.waypointForm.orientationX = Number(waypointInfo.pose.orientation.x).toFixed(3);
            this.waypointForm.orientationY = Number(waypointInfo.pose.orientation.y).toFixed(3);
            this.waypointForm.orientationZ = Number(waypointInfo.pose.orientation.z).toFixed(3);
            this.waypointForm.orientationW = Number(waypointInfo.pose.orientation.w).toFixed(3);
            var yaw = quaternionToYaw(waypointInfo.pose.orientation);
            var yawDeg = yaw / Math.PI * 180;
            this.waypointForm.orientionRAD = Number(yaw).toFixed(3);
            this.waypointForm.orientionDEG = Number(yawDeg).toFixed(3);
        },       

        //关闭模态框
        closeDrawer: function(){
            showWaypointDetail(false);
            this.showMore = true;
        }
    },

    mounted(){
        
    },

    //属性监听
    watch:{
        "$store.state.showWaypointDetail": function(val){
           this.drawer = val;
        },

       '$store.state.wayPointMsg':function(val){
           this.updateWaypointMsg(val)
       }
    }

}
</script>

<style scoped lang="less">
.detailBox{
    position: relative;
    .editBox{
        position: absolute;
        left: 15px;
        top: -52px;
    }
    .goHere{
        position: absolute;
        width: 50px;
        height: 50px;
        background: url("../../src/assets/go.png") no-repeat center;
        background-size: cover;
        right: 15px;
        top: -60px;
    }
}
.waypointForm{
    padding: 0 15px;
    .formItem{
        border-bottom: 1px solid #eeeeee;
        .itemLeft{
            flex: 4;
            text-align: left;
        }
        .itemRight{
            flex: 6;
            height: 40px;
            input{
                width: 100%;
                height: 100%;
                display: block;            
            }
            .selectClass{
                display: flex;
                justify-content: center;
                align-items: center
            }
        }
    }
    .bottomNone{
        border-bottom: none;
    }
    .showMore{
        text-align: center;
        color: #409EFF;
        padding-top: 5px;
        .moreIcon{
            transform:rotate(90deg);
            -ms-transform:rotate(90deg);
            -moz-transform:rotate(90deg);
            -webkit-transform:rotate(90deg);
            -o-transform:rotate(90deg);
        }
    }
     .localTitle{
        text-align: left;
        padding: 10px 0 0 0;
    }
    .localInputItem{
        text-align: center;
        padding: 0 5px;
        .itemLeft{
            text-align: center;
        }
        input{
            border: 1px dashed #eeeeee;
            padding: 5px 0;
            text-align: center;
            margin-top: 5px;
        }
    }
    .stationType{
        input{
            height: 10px;
        }
        .el-input__icon{
            height:inherit
        }
        .el-input__inner{
            height: normal;
            line-height: normal
        }
        width: 100%;
        border: none;
        outline: none;
        padding-left: 0.7em;
        height: 100%;
    }
     .formBtns{
        padding: 0 5px;
        margin-top: 15px;
        button{
            width: 100%;
        }
    }
    .el-form-item{
        margin-bottom: 0;
    }
    .el-form-item__content{
        line-height: 20px;
    }
}
</style>


