<template>
    <div class="collectionBox">
        <div class="head">点击图标派发任务</div>
        <div v-show="this.$store.state.showBattery" ><electricity></electricity></div>
        <div class="colBody flex">
            <el-button type="primary" class="collBtn" icon="el-icon-truck" circle :disabled="disabledBtn" @click="dispatchAGV"></el-button>
        </div>
    </div>    
</template>

<script>
import electricity from '@/components/electricity.vue'
import { navTrack} from "../../js/trajectory"
export default {
    name: 'collection',
    components:{
        electricity
    },
    data(){
        return{
            disabledBtn: false,
            
        }
    },

    //计算属性
    watch:{
        "$store.state.navControlStatus": function(val){
            if(val == 0 || val == 4){
                this.disabledBtn = false;
            }else{
                this.disabledBtn = true;
            }
        }
    },

    mounted(){
        
    },

    methods:{
        dispatchAGV: function(){
            navTrack("Collection_Path_120s");
            this.$notify({
                title: '成功',
                message: '任务派发成功',
                type: 'success'
            });
        }
    }
}
</script>

<style scoped lang="less">
    .collectionBox{
        position: relative;
        color: #303133;  
        .head{
            position: fixed;
            z-index: 1998;
            background: #ffffff;
            width: 100%;
            height: 10vh;
            padding-top: 20px;
            line-height: 3vh;
            text-align: center;
            font-size: 32px;
        }
        .colBody{
            height: 94vh;
            flex-direction: column;
            font-size: 30px;
            .collBtn{
                box-shadow: 0 0 6px rgba(0, 0, 0, .04)
            }
            button{
                padding: 50px;
                font-size: 400px;
            }
        }
        
    }
    /deep/.electricityBox /deep/.progressBox{
        padding-right: 15px;
        width: 200px!important;
    }
    /deep/.el-progress__text{
        font-size: 15px!important;
    }
            /deep/.el-progress-bar__outer{
                height: 30px!important;
            }
</style>


