<template>
    <el-drawer
    :title="this.$t('titles.more')"
    :visible.sync="drawer"
    :close-on-press-escape="false"
    custom-class="authorBox"
    :wrapperClosable="closable"
    :show-close="showClose"
    :direction="direction">

        <previous :param="name"></previous>

        <div class="startMore">
            <el-button  type="primary" @click="guideEndBtn">开启更多</el-button>
        </div>
    </el-drawer>
</template>

<script>
import previous from "./guide/previous.vue"
import { closeGuide } from '../js/methods'
export default {
    name: 'guideEnd',
    components:{
        previous
    },
    data(){
        return{
            drawer: this.$store.state.guideEndModule,
            direction: 'btt',
            closable: false,
            showClose: false,
            name: "end"
        }
    },
    methods:{
      guideEndBtn: function(){
          closeGuide();
      }
    },

    //属性监听
    watch:{
       "$store.state.guideEndModule": function(val){
           this.drawer = val;
       }
    }

}
</script>

<style scoped lang="less">
 .authorCom{
     background-color: #ffffff;
     border-top-left-radius: 4px;
     border-top-right-radius: 4px;
     padding: 10px;
     text-align: left;
     font-size: 14px;
     width: 60vw;
     .startMore{
         text-align: center;
         padding: 15px 0 10px 0;
     }
 }

</style>


